import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import ErrorMessage from '../views/ErrorMessage';

export default class App {
  constructor() {
    this.state = {
      baseSearchURL: 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=',
      searchURL: '',
      baseVideoListURL: 'https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=',
    };
    this.maxPage = 2;

    this.model = new AppModel(this.state);

    this.data = null;

    this.view = new AppView(this.data);

    this.error = new ErrorMessage('Connection error!');
  }

  async start() {
    this.data = await this.model.getClipNames();

    if (this.data && this.data !== 'error') {
      this.data.request = this.request;
    }

    this.view.render(this.data);

    document.getElementById('search-form').addEventListener('submit', this.search.bind(this));

    if (this.data !== null && this.data !== 'error') {
      document.getElementById('next-page-button').addEventListener('click', this.nextPage.bind(this));
      document.getElementById('prev-page-button').addEventListener('click', this.prevPage.bind(this));
      document.getElementById('first-page-button').addEventListener('click', this.firstPage.bind(this));
      document.addEventListener('keydown', this.keyPress.bind(this));

      // for touchscreen
      document.body.addEventListener('touchstart', this.flipStart.bind(this));
      document.body.addEventListener('touchmove', this.flipMove.bind(this));
      document.addEventListener('touchend', this.flipEnd.bind(this));
      // for mouse
      document.body.addEventListener('mousedown', this.flipMouseStart.bind(this));
      document.addEventListener('mouseup', this.flipMouseEnd.bind(this));
    }

    this.maxPage = 2;
  }

  keyPress(event) {
    if (event.code === 'ArrowRight' || event.code === 'ArrowUp' || event.code === 'PageUp') {
      this.nextPage();
    }
    if (event.code === 'ArrowLeft' || event.code === 'ArrowDown' || event.code === 'PageDown') {
      this.prevPage();
    }
    if (event.code === 'Digit1' || event.code === 'Home') {
      this.firstPage();
    }
  }

  // for touchscreen
  flipStart(startEvent) {
    this.startTouches = startEvent.changedTouches;
  }

  flipMove(moveEvent) {
    const moveTouches = moveEvent.changedTouches;
    const difference = {
      x: moveTouches[0].pageX - this.startTouches[0].pageX,
      y: moveTouches[0].pageY - this.startTouches[0].pageY,
    };

    const page = this.view.clipList.getPage();

    document.getElementById('content').style.transform = `
      translateX(calc(${-100 * page}vw + ${difference.x}px))
    `;
  }

  flipEnd(endEvend) {
    const endTouches = endEvend.changedTouches;
    const difference = {
      x: endTouches[0].pageX - this.startTouches[0].pageX,
      y: endTouches[0].pageY - this.startTouches[0].pageY,
    };

    const SWIPE_DISTANCE = 50;

    const page = this.view.clipList.getPage();

    // swipe too short to flip
    if (difference.x <= SWIPE_DISTANCE && difference.x >= -SWIPE_DISTANCE) {
      document.getElementById('content').style.transform = `
      translateX(${-100 * page}vw)
      `;
    }
    // flip back
    if (difference.x < -SWIPE_DISTANCE) {
      this.nextPage();
    }
    // flip forward
    if (difference.x > SWIPE_DISTANCE) {
      this.prevPage();
    }
  }

  // for mouse
  flipMouseStart(startEvent) {
    this.startTouches = startEvent;
    this.moving = true;
    document.body.addEventListener('mousemove', this.flipMouseMove.bind(this));
  }

  flipMouseMove(moveEvent) {
    moveEvent.preventDefault();
    if (this.moving) {
      const difference = {
        x: moveEvent.clientX - this.startTouches.clientX,
        y: moveEvent.clientY - this.startTouches.clientY,
      };

      const page = this.view.clipList.getPage();

      document.getElementById('content').style.transform = `
        translateX(calc(${-100 * page}vw + ${difference.x}px))
      `;
    }
  }

  flipMouseEnd(endEvend) {
    const difference = {
      x: endEvend.clientX - this.startTouches.clientX,
      y: endEvend.clientY - this.startTouches.clientY,
    };

    const SWIPE_DISTANCE = 50;

    const page = this.view.clipList.getPage();

    // swipe is too short to flip
    if (difference.x <= SWIPE_DISTANCE && difference.x >= -SWIPE_DISTANCE) {
      document.getElementById('content').style.transform = `
      translateX(${-100 * page}vw)
      `;
    }
    // flip back
    if (difference.x < -SWIPE_DISTANCE) {
      this.nextPage();
    }
    // flip forward
    if (difference.x > SWIPE_DISTANCE) {
      this.prevPage();
    }

    this.moving = false;
  }

  async nextPage() {
    const page = this.view.clipList.nextPage();
    if (page === this.maxPage) {
      this.maxPage += 3;
      const nextPageData = await this.model.getNextPage();
      this.view.clipList.extendList(nextPageData);
    }
  }

  prevPage() {
    this.view.clipList.prevPage();
  }

  firstPage() {
    this.view.clipList.firstPage();
  }

  async search(event) {
    event.preventDefault();
    this.request = document.getElementById('search-input').value;
    if (this.request) {
      this.state.searchURL = this.state.baseSearchURL + this.request;
    }

    this.start();
  }
}
