export default class SearchBar {
  constructor() {
    this.hint = 'Search';
  }

  render(data) {
    let request = '';
    if (data) {
      ({ request } = data);
    }
    document.body.innerHTML = `
      <form id="search-form" class="search-form">
        <input id="search-input" class="search-input" type="text" placeholder="${this.hint}"
        value="${request}"></input>
        <button id="search-button" class="search-button" type="submit" value="submit"></button>
      </form>
    `;
  }
}
